<template>
    <div class="leads-info">
        <p class="title">Leads-100002xx</p>
        <p class="sub-title">Leads Detail</p>
        <div class="leads-info-input">
            <ul>
                <li>
                    <p class="input-title">Leads ID</p>
                    <el-input v-model="leadsId" placeholder="自動帶入"></el-input>
                </li>
                <li>
                    <p class="input-title">會員 ID</p>
                    <el-input v-model="id" placeholder="自動帶入"></el-input>
                </li>
                <li>
                    <p class="input-title">姓名</p>
                    <el-input v-model="username" placeholder="自動帶入"></el-input>
                </li>
                <li>
                    <p class="input-title">暱稱</p>
                    <el-input v-model="nickname" placeholder="自動帶入"></el-input>
                </li>
                <li>
                    <p class="input-title">縣市</p>
                    <el-input v-model="city" placeholder="自動帶入"></el-input>
                </li>
                <li>
                    <p class="input-title">鄉鎮</p>
                    <el-input v-model="township" placeholder="自動帶入"></el-input>
                </li>
                <li>
                    <p class="input-title">E-mail</p>
                    <el-input v-model="mail" placeholder="自動帶入"></el-input>
                </li>
                <li>
                    <p class="input-title">公司名稱</p>
                    <el-input v-model="company" placeholder="自動帶入"></el-input>
                </li>
                <li>
                    <p class="input-title">統一編號</p>
                    <el-input v-model="numbering" placeholder="自動帶入"></el-input>
                </li>
                <li>
                    <p class="input-title">來源</p>
                    <el-input v-model="source" placeholder="自動帶入"></el-input>
                </li>
                <li>
                    <p class="input-title">品牌</p>
                    <el-input v-model="brand" placeholder="自動帶入"></el-input>
                </li>
                <li>
                    <p class="input-title">型號</p>
                    <el-input v-model="model" placeholder="自動帶入"></el-input>
                </li>
                <li class="leads-info-input-w100">
                    <p class="input-title">其他配備</p>
                    <el-input
                        type="textarea"
                        :autosize="{ minRows: 4, maxRows: 10}"
                        placeholder="请输入内容"
                        v-model="equipped">
                    </el-input>
                </li>
                <li class="leads-info-input-w100">
                    <p class="input-title">Follow 狀態</p>
                    <el-select v-model="value" placeholder="请选择">
                        <el-option
                            v-for="item in follow"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </li>
                <li class="leads-info-input-w100">
                    <p class="input-title">Leads 備註</p>
                    <el-input
                        type="textarea"
                        :autosize="{ minRows: 4, maxRows: 10}"
                        placeholder="请输入内容"
                        v-model="remark">
                    </el-input>
                </li>
                <li class="leads-info-input-w100">
                    <p class="input-title">註冊日期</p>
                    <el-input v-model="joinDate" :disabled="true" placeholder="儲存成功後即顯示"></el-input>
                </li>
                <li class="leads-info-input-w100">
                    <p class="input-title">最後修改日期日期</p>
                    <el-input v-model="reviseDate" :disabled="true" placeholder="儲存成功後即顯示"></el-input>
                </li>

            </ul>
            
        </div>
        <div class="car-btn">確認建立</div>
    </div>
</template>

<script>
export default {
    name:'leadsInfo',
    components: {
    },
    data() {
        return {
            leadsId:'10002xx',
            id:'123456789',
            username:'王曉明',
            nickname:'',
            city:'台中',
            township:'西屯',
            mail:'',
            company:'',
            numbering:'',
            joinDate: '',
            reviseDate: '',
            source:'收藏',
            brand:'',
            model:'',
            equipped:'',
            remark: '',
            follow: [{
                value: '选项1',
                label: '待 Follow'
                }, {
                value: '选项2',
                label: '已 Follow'
                }],
            value: '',
            }
    }


}
</script>

<style lang='scss' scoped>
.leads-info {
    padding: 100px 0;
    text-align: center;
    max-width: 850px;
    width: 80%;
    margin: 0 auto;
    .title {
        color: #333333;
        font-size: 40px;
        font-weight: bold;
    }
    .sub-title {
        color: #ef6d38;
        font-size: 25px;
    }
    .leads-info-input {
        margin-top: 50px;
        text-align: left;
        .input-title {
            color: #ef6d38;
            padding-left: 25px;
            margin-bottom: 10px;
            text-align: left;
        }
        ul {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            li {
                margin-top: 30px;
                padding:0 10px;
            }
        }
        .img-box {
            background: #f5f5f5;
            padding: 20px;
            text-align: center;
        }
        .leads-info-input-w100 { 
            width: 105%;
        }        
    }

    @media screen and (max-width: 1540px) {
        >>> .el-input .el-input__inner{
             width: 100% !important;
         }
        .leads-info-input ul li{
            width: 100%;
        }
        .leads-info-input-w100{
            max-width: none !important;
        }
        .input-title{
            max-width: none !important;
            width: 100% !important;
        }
    }

    @media screen and (max-width: 768px) {
        padding-top: 20px;
        width: 90%;
        .leads-info-input-w100 { 
            width: 105%;
        }

    }
}

</style>


