<style lang='scss' scoped>
.leads {
    width: 100%;
    @media screen and (max-width: 768px){
        padding-top: 50px;
    }
}
.leads-list {
    text-align: center;
    max-width: 1200px;
    width: calc(100vw - 495px);
    margin: 0 auto;
    padding: 0 20px;
    box-sizing: border-box;
    overflow: hidden;
    .title {
        color: #333333;
        font-size: 40px;
        font-weight: bold;
    }
    .sub-title {
        color: #ef6d38;
        font-size: 25px;
        margin-bottom: 50px;
    }
    .leads-input {
        text-align: left;
        
    }
    .leads-table {
        margin: 50px 0;
    }
    .btn {
        background: #ef6d38;
        border-radius: 5px;
        padding: 10px;
        color: #fff;
        width: 70px;
        text-align: center;
        transition: all .3s;
        cursor: pointer;
        &:hover {
            background: #d65c2c
        }
    }
    @media screen and (max-width: 900px){
        width: 100%;
        padding-top: 50px;
    }
}

</style>


